<template>
	<view class="cart">
		<headthree title="购物车" :stat="stat" :widstat="widstat" @headevent="headevent"></headthree>
		<view class="contbox" :style="{'top':topheight+'px','height':'calc(100vh - '+topheight+'px - '+domheight+'px )'}">
			<scrollone :loadtopstat="loadtopstat" @scrolltolower="scrolltolower" @refresherrefresh="refresherrefresh">
					<modtyone :cartdata="cartdata" @select="select" @specibut="specibut" @jianbut="jianbut" @addbut="addbut"></modtyone>
			</scrollone>
		</view>
		<tabarone :zonjimuch="zonjimuch" :selectstat="selectstat" :widstat="widstat" @tabarevent="tabarevent" @tabarselect="tabarselect"></tabarone>
	</view>
</template>

<script>
	import headthree from '../../components/head/headthree/headthree.vue'
	import scrollone from "../../components/scroll/scrollone/scrollone.vue"
	import tabarone from '../../components/tabar/tabarone/tabarone.vue'
	import modtyone from '../../components/modty/modtyone/modtyone.vue'
	export default {
		name:"cart",
		data() {
			return {
				stat:false,//tabar购物车
				topheight:45,//滚动窗体距离头部值
				domheight:50,//滚动窗体距离底部值
				
				widstat:true,//购物车操作模式 删除、完成
				selectstat:true,//全选
				zonjimuch:0.0,//总计价格
				loadtopstat:false,//下拉状态
				
				cartdata:[{//购物车数据
					id:1,
					shopnum:5,
					skuid:1,
					stat:1,
					shopdata:{
						name:'新疆香酥脆枣空心红枣小包装零食',
						headimg: require('../../static/img/shop.jpg'),
						type:require('../../static/img/type.png'),
						speci:'250g',
						price:'9.90',
						stock:'10',
						stat:'1',
					}
				},{
					id:1,
					shopnum:5,
					skuid:1,
					stat:0,
					shopdata:{
						name:'新疆香酥脆枣空心红枣小包装零食',
						headimg: require('../../static/img/shop.jpg'),
						type:require('../../static/img/type.png'),
						speci:'250g',
						price:'9.90',
						stock:'10',
						stat:'2',
					}
				}]
			}
		},
		components:{
			headthree,
			scrollone,
			modtyone,
			tabarone
		},
		//组件创建完毕执行
		created(){
			/* #ifdef  MP-WEIXIN */
			this.topheight = uni.getSystemInfoSync().statusBarHeight+40;//顶部距离
			/* #endif */
		},
		methods: {
			//接受到来自头部组件的模式转换（管理|完成）
			headevent(str){
				this.widstat = !this.widstat;
			},
			//接收到来自底部导航的（去下单||删除）事件
			tabarevent(str){
				if(this.widstat){
					//去下单
					console.log("去下单");
				}else{
					//删除购物车选中的商品
					console.log("删除选中");
				}
			},
			//接收到来自底部导航的全选事件
			tabarselect(str){
				this.selectstat = !this.selectstat;
				console.log("全选");
			},
			//收到导航条发出的触底事件
			scrolltolower(){
				if(this.loaddomstat){
					console.log("还在加载");
					return false;
				}
				this.loaddomstat = true; 
				console.log("开始加载");
				setTimeout(() => {
					console.log("加载完成");
				    this.loaddomstat = false;//关闭加载
				}, 3000)
			},
			//收到导航条下拉刷新事件
			refresherrefresh(str){
				console.log("开始加载");
				this.loadtopstat = true;//开启加载
				setTimeout(() => {
					console.log("加载完成");
				    this.loadtopstat = false;//关闭加载
				}, 3000)
			},
			//接收到来自商品的选中事件
			select(str){
				//更新购物车数据
				console.log(str);
			},
			//接收到来自商品的加商品事件
			addbut(str){
				//更新购物车数据
				console.log(str);
			},
			//接收到来自商品的减商品事件
			jianbut(str){
				//更新购物车数据
				console.log(str);
			},
			//接收到来自商品的选择规格事件
			specibut(str){
				//更新购物车数据
				console.log(str);
			},
			//计算选中商品总价格
		}
	}
</script>
<style scoped>
.contbox{
	position: fixed;
	left: 0px;
	width: 100%;
	overflow: hidden;
}
</style>