<!-- 自定义头部3 -->
<template>
	<view class="headthree" :style="{'padding-top': topheight +'px'}">
		<view class="headbox" v-if="stat" @click="fanhui">
			<image class="img" src="../../../static/img/fanhui.png" mode="aspectFill"></image>
		</view>
		<view class="cont">{{ title }}</view>
		<view class="headbox">
			<!-- #ifdef  H5 || APP-PLUS -->
			<span  @click="headevent">{{ widstat ? '管理' : '完成'}}</span>
			<!-- #endif -->
		</view>
		
	</view>
</template>

<script>
	export default {
		name:"headthree",
		data() {
			return {
				topheight:0,//距离顶部的高
			}
		},
		props:['stat','title','widstat'],
		//组件挂载完毕
		created(){
			this.topheight = uni.getSystemInfoSync().statusBarHeight+5;//顶部距离
		},
		methods: {
			fanhui(){
				this.$emit('fanhui');
			},
			headevent(){
				this.$emit('headevent',null);
			}
		},
	}
</script>

<style lang="scss" scoped>
.headthree{
	position: fixed;
	top: 0px;
	left: 0px;
	z-index: 10;
	width: 100%;
	/* #ifdef  H5 || APP-PLUS */
	height: 40px;
	line-height: 40px;
	/* #endif */
	/* #ifdef  MP-WEIXIN */
	height: 35px;
	line-height: 30px;
	/* #endif */
	background: $uni-color-color;
	color: #fff;
	display: flex;
	justify-content: space-between;
}
.cont{
	margin: 0px 3%;
	font-size: $uni-title-font;
}
.headbox{
	/* #ifdef  H5 || APP-PLUS */
	width: 40px;
	height: 40px;
	line-height: 40px;
	/* #endif */
	/* #ifdef  MP-WEIXIN */
	width: 30px;
	height: 30px;
	line-height: 30px;
	/* #endif */
	text-align: center;
	font-size: $uni-text-font;
}
.img{
	width: 20px;
	height: 20px;
	/* #ifdef  H5 || APP-PLUS */
	padding: 10px;
	/* #endif */
	/* #ifdef  MP-WEIXIN */
	padding:5px;
	/* #endif */
}
</style>
