<!-- 底部导航1 -->
<template>
	<view class="tabarone">
		<view class="select" @click="tabarselect">
			<p :class="selectstat ? 'yes':''"></p>
			<span>全选</span>
		</view>
		<view v-if="widstat"  class="price">
			总计：<span>￥{{ zonjimuch }}</span>
		</view>
		<view  class="but" @click="tabarevent">
			<view v-if="widstat">去结算</view>
			<view v-if="!widstat">删除</view>
			<image class="but-image" v-if="widstat" src="../../../static/img/gopay.png"></image>
			<image class="but-image" v-if="!widstat" src="../../../static/img/deleone.png"></image>
		</view>
	</view>
</template>

<script>
	export default {
		name:"tabarone",
		data() {
			return {
			}
		},
		props:['zonjimuch','selectstat','widstat'],
		methods: {
			//去下单、删除选中事件
			tabarevent(){
				this.$emit('tabarevent',null);
			},
			//全选事件
			tabarselect(){
				this.$emit('tabarselect',null);
			}
		}
	}
	
</script>

<style lang="scss" scoped>
.tabarone{
	position: fixed;
	bottom: 0px;
	left: 0px;
	width: 100%;
	height: 50px;
	display: flex;
	align-items: center;
	justify-content: space-between;
	background: #ffffff;
	box-shadow: 0px 0px 2px #333;
}
.select{
	height: 20px;
	padding: 15px;
	font-size: 12px;
	color: #333;
	display: flex;
}
.select p{
	width: 18px;
	height: 18px;
	border: 1px solid #eee;
	border-radius: 50%;
}
.select p.yes{
	background:url(../../../static/img/select.png) no-repeat;
	background-size: 18px 18px;
}
.select span{
	padding-left: 5px;
}
.price{
	flex: 1;
	text-align: center;
}
.price span{
	color: red;
}
.but{
	width: 100px;
	height: 20px;
	padding: 15px;
	color: #ffffff;
	background: $uni-color-color;
	display: flex;
	justify-content: center;
	align-items: center;
}
.but .but-image{
	width: 16px;
	height: 16px;
	margin-bottom: 2px;
}
</style>
